import React, { Component}  from 'react';
import jQuery from 'jquery'; 
import moment from 'moment';
import DatePicker from 'react-datepicker';
import Cookies from 'universal-cookie';
import apiUrl from '../../settings.js';

const cookies = new Cookies();
class EditAppointmentForm extends Component{
    constructor(props) {
    super(props);

    this.state = {
                        Medication: '',
                        Medication_valid: true,
                        Date: '',
                        Date_valid: true,
                        Log: '',
                        Log_valid: true,
                        Response_txt: '',
                        Error: false,
      
    }
    this.submitFormHandler = this.submitFormHandler.bind(this);
                this.handleInputChange = this.handleInputChange.bind(this);
                this.handleInputFocus = this.handleInputFocus.bind(this);
                this.back = this.back.bind(this);
    this.handleDateChange = this.handleDateChange.bind(this);
  }
  
  back() {
      window.location.replace(window.location.origin + '/appointments/'+this.props.match.params.patientId);
  }
  
  componentWillMount() {
      var url = apiUrl + '/users/search?'
                +'&Email='+this.props.match.params.email;
        if (!(cookies.get('Id') === undefined))
        {
            url += '&Id='+ cookies.get('Id').toString();   
        }
               
        jQuery.getJSON(url,function (res){
             if (res.Error)
            {
                this.setState({Response_txt : res.Error});
                this.setState({Error : true});
            } else {
                var appointment = {};
                for (var i=0;i<res[0].Appointments.length;i++) {
                    if (res[0].Appointments[i].Id == this.props.match.params.id) {
                        appointment = res[0].Appointments[i];
                    }
                }
               this.setState({Medication : appointment.Medication}); 
               this.setState({Date : appointment.Date.substr(0,16)}); 
               this.setState({Log : appointment.Log}); 
            }
        }.bind(this));
  }
  
  
  submitFormHandler(event) {
                if (this.state.Medication === "") {
        this.setState({Medication_valid: false});
        } else if (this.state.Date === "") {
        this.setState({Date_valid: false});
        } else if (this.state.Log === "") {
        this.setState({Log_valid: false});
        }  else {
        var url = apiUrl + '/appointment/edit?Log='+this.state.Log
                +'&Id='+this.props.match.params.id
                +'&Email='+this.props.match.params.email
                +'&Date='+this.state.Date
                +'&Medication='+this.state.Medication;
        if (!(cookies.get('Id') === undefined))
        {
            url += '&CurrentUserId='+ cookies.get('Id').toString();   
        }
               
        jQuery.getJSON(url,function (res){
             if (res.Error)
            {
                this.setState({Response_txt : res.Error});
                this.setState({Error : true});
            } else {
               this.setState({Error: false});
               this.setState({Response_txt : res.Ok});
               this.back();
            }
        }.bind(this));
        
        }
        event.preventDefault();
        }

        handleInputChange(event) {
        this.setState({
        [event.target.name]: event.target.value
        })
        }

        handleDateChange(date) {
        this.setState({
        Date: date.format('YYYY-MM-DDTHH:mm')
        })
        }

        handleInputFocus(event) {
        this.setState({
        [event.target.name + '_valid'] : true
        })
        }
  
Response () {
        const danger = this.state.Error ? <div className="text-danger">{this.state.Response_txt}</div> : <div>{this.state.Response_txt}</div>;
        return (<div className="form-group">
    {danger}
    </div>
                        )
        }

        Medication () {
        const danger = this.state.Medication_valid ? '' : <div className="text-danger">Enter Medication.</div>;
                return (
<div className="form-group">
    <label>Medication</label>
    <input type="text" className="form-control" value={this.state.Medication} name="Medication" onChange={this.handleInputChange} onFocus={this.handleInputFocus} required/>
    {danger}
</div>
                        )
        }

        Date () {
        const danger = this.state.Date_valid ? '' : <div className="text-danger">Enter Date of Appointment.</div>;
                return (
<div className="form-group">
    <label>Date of Appointment</label>
    <DatePicker type="text" showYearDropdown showTimeSelect className="form-control" selected={(this.state.Date == '' ?  moment() : moment(this.state.Date) )} dateFormat="YYYY-MM-DDTHH:mm" name="Date" onChange={this.handleDateChange} onFocus={this.handleInputFocus} required/>
    {danger}
</div>
                        )
        }

        
        Log () {
        const danger = this.state.Log_valid ? '' : <div className="text-danger">Enter Log.</div>;
                return (
<div className="form-group">
    <label>Log</label>
    <textarea rows="5" className="form-control" value={this.state.Log} name="Log" onChange={this.handleInputChange} onFocus={this.handleInputFocus} required />
    {danger}
</div>
                        )
        }

       


        render () {
        return (
<div className="container">
    <form onSubmit={this.submitFormHandler} className="col-lg-6 col-lg-offset-3">
        <h1>Edit appointment</h1>
        {this.Response()}
        {this.Medication()}
        {this.Date()}
        {this.Log()}
        <button onClick={this.back} className="btn btn-default pull-left" type="button"><span className="glyphicon glyphicon-arrow-left"></span>&nbsp;&nbsp;Back</button>
        <button type="submit" className="btn btn-primary pull-right"><span className="glyphicon glyphicon-ok"></span>&nbsp;&nbsp;Save</button>
    </form>
</div>
                );
        }
        }

export default EditAppointmentForm;



