import React, { Component}  from 'react';
import jQuery from 'jquery'; 
import Cookies from 'universal-cookie';
import { Switch, Route, Link } from 'react-router-dom'
import apiUrl from '../../settings.js';

const cookies = new Cookies();

class AppointmentsForm extends Component{
    constructor(props) {
    super(props);

    this.state = {
      patient:{},
      Response_txt: '',
      Error: false,
    }

  }
 
  componentWillMount() {
      var url = apiUrl + '/users/findById?Id='+this.props.match.params.id;
               
        jQuery.getJSON(url,function (res){
             if (res.Error)
            {
                this.setState({Response_txt : res.Error});
                this.setState({Error : true});
            } else {
               this.setState({Error: false});
               res.Appointments.sort(function (a, b) {
                   if (a.Date > b.Date) {
                       return 1;
                   }
                   if (a.Date < b.Date) {
                       return -1;
                   }
                   return 0;
               });
               this.setState({patient:res});
            }
        }.bind(this));
    }
    
    Response () {
        const danger = this.state.Error ? <div className="text-danger">{this.state.Response_txt}</div> : <div>{this.state.Response_txt}</div>;
        return (<div className="form-group">
    {danger}
    </div>
                        )
        }
        
        

Table (props) {
    const controlButtonStyle = {
        padding: '3px 8px'
    }
    const groupButtonStyle = {
        width: '100%'
    }
    const ButtonStyle = {
        width: '100%'
    }
    const appointments = props.patient.Appointments ? props.patient.Appointments.map((appointment) => <tr>
    <td>{appointment.Medication}</td>
    <td>{appointment.Date.replace(/[T]{1}/,' ').substr(0,16)}</td>
    <td>{appointment.Log}</td>
    <td>{appointment.DoctorID.FullName}</td>
    <td style={controlButtonStyle}>
        <div className="btn-group" role="group" style={groupButtonStyle}>
	    <button type="button" className="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" style={ButtonStyle}>Action&nbsp;&nbsp;<span className="caret"></span></button>
            <ul className="dropdown-menu">
                <li><Link to={`/appointments/edit/${appointment.Id}/${props.patient.Email}/${props.patient._id}`}><span className="glyphicon glyphicon-pencil"></span>&nbsp;&nbsp;Edit</Link></li>
                <li><Link to={`/appointments/delete/${appointment.Id}/${props.patient.Email}/${props.patient._id}`}><span className="glyphicon glyphicon-trash"></span>&nbsp;&nbsp;Delete</Link></li>
            </ul>
        </div>
    </td>        </tr>) : null;
    
    const headerButtonStyle = {
        padding: '3px 8px'
    }
    
        return (<table className="table table-striped table-bordered">
        <thead>
        <tr>
    <th>Medication</th>
    <th>Date</th>
    <th>Log</th>
    <th>Doctor's Name</th>
    <th style={headerButtonStyle}><Link to={`/appointments/add/${props.patient.Email}/${props.patient._id}`} className="btn btn-primary form-control"><span className="glyphicon glyphicon-plus"></span> Add</Link></th>
   </tr>
   </thead>
      <tbody>
    {appointments}
       </tbody>
    </table>
                        )
        }

  render () {
    return (
      <div className="container">
          <h1>Appointments</h1>
          {this.Response()}
          <this.Table  patient = {this.state.patient}/>
      </div>
    );
  }
}

export default AppointmentsForm;



