import React, { Component } from 'react';
import './App.css';
import LoginForm from './public/login.js';
import RegisterForm from './public/signup.js';
import AddUserForm from './public/users/add.js';
import DeleteUserForm from './public/users/delete.js';
import EditUserForm from './public/users/edit.js';
import UsersForm from './public/users/all.js';
import PatientsWithDoctorForm from './public/users/patientsWithDoctor.js';
import AppointmentsForm from './public/appointments/forDoctor.js';
import PatientAppointmentsForm from './public/appointments/forPatient.js';
import ResetPasswordForm from './public/resetPassword.js';
import AddAppointmentForm from './public/appointments/add.js';
import EditAppointmentForm from './public/appointments/edit.js';
import DeleteAppointmentForm from './public/appointments/delete.js';
import BindDoctorForm from './public/bindDoctor.js';
import ErrorPathForm from './public/ErrorPath.js';
import apiUrl from './settings.js';

import 'bootstrap/dist/js/bootstrap';
import { Switch, Route, Link } from 'react-router-dom'
import jQuery from 'jquery'; 
import Cookies from 'universal-cookie';
const cookies = new Cookies();


class App extends Component {
    
    constructor(props) {
    super(props);
    
    this.state = {
        Type: '',
        Id: '',
        Name: '',
        Temp: 0,
        WeatherIcon: '',
        Weatherstatus: false,
        City: '',
    }

    this.exit = this.exit.bind(this);
  }
  
  componentWillMount() {
     var url = apiUrl + '/users/findById?';
        if (!(cookies.get('Id') === undefined))
        {
            url += 'Id='+ cookies.get('Id').toString();   
        }
               
        jQuery.getJSON(url,function (res){
             if (!res.Error) {
               this.setState({Type: res.Type});
               this.setState({Id: res._id});
               this.setState({Name: res.FullName});
               var p = this;
               jQuery.getJSON( window.location.protocol + "//api.openweathermap.org/data/2.5/weather?q="+res.City+"&APPID=77036b5cd46e1e98611b13552b0bdf95",function (ans){
                   p.setState({Temp: (ans.main.temp - 273.15).toFixed(1) });
                   p.setState({WeatherIcon: ans.weather[0].icon});
                   p.setState({Weatherstatus: true});
                   p.setState({City: ans.name });
               });
            }
        }.bind(this));
  }
  
  exit(event) {
      cookies.remove('Id');
      window.location.replace(window.location.origin + '/login');
  }

  Main() {
    return (
      <main>
        <Switch>
          <Route exact path='/' component={LoginForm} />
          <Route path='/login' component={LoginForm} />
          <Route path='/register' component={RegisterForm} />
          <Route exact path='/users' component={typeof cookies.get('Id') !== "undefined" ? UsersForm : ErrorPathForm} /> 
          <Route exact path='/users/add' component={AddUserForm} />   
          <Route exact path='/users/delete/:email/:type' component={DeleteUserForm} /> 
          <Route exact path='/users/edit/:email/:type' component={EditUserForm} /> 
          <Route path='/resetPassword/:email' component={ this.state.Type === 'Admin' ? ResetPasswordForm : ErrorPathForm} />
          <Route path='/resetPassword' component={this.state.Type === 'Admin' ? ResetPasswordForm : ErrorPathForm} />
          <Route exact path='/appointments/:id' component={AppointmentsForm} /> 
          <Route exact path='/appointments' component={PatientAppointmentsForm} /> 
          <Route exact path='/appointments/add/:email/:patientId' component={AddAppointmentForm} /> 
          <Route exact path='/appointments/edit/:id/:email/:patientId' component={EditAppointmentForm} />  
          <Route exact path='/appointments/delete/:id/:email/:patientId' component={DeleteAppointmentForm} />  
          <Route exact path='/bind/:patientId/:doctorId' component={BindDoctorForm} />  
          <Route path='/patients/:doctorId' component={PatientsWithDoctorForm} />
          <Route component={ErrorPathForm} />
        </Switch>
      </main>
    );
  }

 ResetPass(props) {
      return  props.Type == 'Admin' ? <li><Link to={`/resetPassword`}>Reset password</Link></li> : null;
    }
    
 PatientAppointments(props) {
      return  props.Type == 'Patient' ? <li><Link to={`/appointments`}>Appointments</Link></li> : null;
    }  
    
 Users(props) {
     const users = props.Type == 'Doctor' ? 'Patients' : 'Doctors';
      return  props.Type == 'Admin' || props.Type == 'Doctor' || props.Type == 'Patient' ? <li><Link to={`/users`}>{users}</Link></li> : null;
    }
   
 Login(props) {
      return  props.Type == '' ? <li><Link to={`/login`}>Login</Link></li> : null;
    }
    
  Register(props) {
      return  props.Type == '' ? <li><Link to={`/register`}>Register</Link></li> : null;
    }
    
    
  Exit(props) {
    const style = {
      cursor: "pointer"
    }
    return  props.Type != '' ? <li><a onClick={props.Func} style={style}>Exit</a></li> : null;
  }  
    
  GreetingMessage (props) {
     return props.Name != '' ? ", " + props.Name : "";
  }

  Weather(props) {
    if ( props.Status ) {
      return (
        <ul className="nav navbar-nav">
          <li><a>{props.City}: {props.Temp + '°C'}</a></li>
          <li><img src={"/weather/" + props.Icon + ".png"} /></li>
        </ul>
      );
    }
    return "";
  }

  Header() {
    const weatherIconContainerStyle = { padding: 0};
    return (
      <nav className="navbar navbar-default">
        <div className="container">
            <div className="navbar-header">
              <div className="navbar-brand">Hello<this.GreetingMessage Name={this.state.Name} />!</div>
            </div>
            
            <this.Weather Icon={this.state.WeatherIcon} City={this.state.City} Temp={this.state.Temp} Status={this.state.Weatherstatus} />

            <ul className="nav navbar-nav navbar-right">
              <this.Login Type={this.state.Type} />
              <this.Register Type={this.state.Type} />
              <this.Users Type={this.state.Type} />
              <this.ResetPass Type={this.state.Type} />
              <this.PatientAppointments Type={this.state.Type} Id={this.state.Id} />
              <this.Exit Type={this.state.Type} Func={this.exit} />
            </ul>
        </div>
      </nav>
    );
  }

  render() {
    const Header = <div/>;

    return (
      <div className="App">
        {this.Header()}
        {this.Main()}
      </div>
    );
  }
}

export default App;



